.experienceObject-container {
    padding: 0px 0px 10px 0px;
    &>.section {
        height: 40px;
        padding: 0px 20px 0px 20px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
    }

    ul {
        display: flex;
        flex-direction: column;
        // border: 1px solid black;
    }

    li {
        font-style: italic;
        border: none;
        text-align: left;
        vertical-align: bottom;
    }
}