.about-container {
  margin: 0px;
  padding: 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .body-text {
    font-family: "Sora", sans-serif;
    font-weight: 250;
    font-optical-sizing: auto;
  }

  .resume-link {
    width: max-content;
  }

  & > .main-bg {
    max-width: 100%;
  }
  & > .short-description-div {
    & > .intro-text {
      display: flex;
      flex-direction: column;
      width: 40%;
      padding: 20px;
      .avatar {
        width: 80px;
        height: 80px;
      }
    }
  }
}
// justify-content: center;
@media only screen and (max-width: 800px) {
  .about-container {
    .resume-link {
      width: auto;
    }

    .body-text {
      font-size: 14px;
    }

    & > .short-description-div {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      & > .intro-text {
        width: 100vw;
        .avatar {
          width: 56px;
          height: 56px;
        }
      }
    }
  }
}
