.top-bar-container {
  position: fixed;
  width: 100vw;
  height: 8vh;
  z-index: 10000;

  background: black;
  display: flex;
  justify-content: space-between;
  align-items: center;

  & > .right-halve {
    display: flex;
    justify-content: space-evenly;
    width: 30%;

    & > button {
      color: white;
      height: 7vmin;
      min-height: 38px;
      margin: 0px, 5px, 0px, 5px;
    }

    & > button:hover {
      background: rgba(189, 191, 194, 0.5);
    }
  }
}

@media only screen and (max-width: 600px) {
  .top-bar-container {
    .left-halve {

    }
    .right-halve {
      width: 100vw;
    }
  }
}
