.mobile-app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-left: 20px;
  padding-right: 20px;


  .body-text {
    font-family: "Sora", sans-serif;
    font-weight: 250;
    font-optical-sizing: auto;
  }

  .hStack-pic {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  .motion-pic {
    background: lightgray;
    box-shadow: 0 11px 15px -7px rgb(0 0 0 / 20%), 0 24px 38px 3px rgb(0 0 0 / 14%), 0 9px 46px 8px rgb(0 0 0 / 12%);
    border-radius: 40px;
    border: 5px solid black;
    width: 20%;
  }

  .motion-video {
    background: lightgray;
    box-shadow: 0 11px 15px -7px rgb(0 0 0 / 20%), 0 24px 38px 3px rgb(0 0 0 / 14%), 0 9px 46px 8px rgb(0 0 0 / 12%);
    border-radius: 30px;
    width: 20%;
  }

  .motion-card-center {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .motion-card-parent {
    & > .motion-card-child {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      width: 50%;
      text-align: left;
      margin-bottom: 20px;
    }
  }

  .tech-stack-div {
    margin: 20px;
  }

  .hStack-tech-stack {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
}

@media only screen and (max-width: 800px) {
  .mobile-app-container {
    padding: 0px;

    .body-text {
      font-size: 14px;
    }

    .motion-card-parent {
      & > .motion-card-child {
        display: flex;
        flex-direction: column;
        width: 90%;
        text-align: left;
      }
    }

    .motion-video {
      visibility: hidden;
      height: 0;
    }

    .tech-stack-div {
    }

    .hStack-pic { // It is now a zStack
      position: relative;
      border-radius: 20px;
      width: 95vw;
      height: 60vmax;
      margin-bottom: 20px;
      overflow: hidden;
      border: 1px solid lightgray;
      & > .zStack-pic-e {
        position: absolute;
        box-shadow: rgba(149, 157, 165, 0.3) 0px 12px 48px 0px;
        width: 75%;
        top: 5%;
      }
    }

    .hStack-tech-stack {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
      gap: 0px; /* Adjust the gap between columns as needed */
      width: 90vw;
      padding: 0;
    }
  }
}
