.desktop-app-container {
  display: flex;
  flex-direction: column;
  padding-right: 20px;

  .body-text {
    font-family: "Sora", sans-serif;
    font-weight: 250;
    font-optical-sizing: auto;
  }

  .heading-text {
  }

  .hStack-tech-stack {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .zStack {
    position: relative;
    min-width: 64vw;
    height: calc(64vw * 0.53); // div height
    margin-bottom: calc(1.6 * 64vw * 0.53); // (80% + 80%) * div height = 160% * div height = 1.6 * div height

    & > .left-pic-e {
      position: absolute;
      left: 0;
    }

    & > .right-pic-e {
      position: absolute;
      left: 20%;
    }
  }

  .motion-pic {
    background: lightgray;
    // box-shadow: 0 11px 15px -7px rgb(0 0 0 / 20%), 0 24px 38px 3px rgb(0 0 0 / 14%), 0 9px 46px 8px rgb(0 0 0 / 12%);
    box-shadow: 0 11px 15px -7px rgb(0 0 0 / 20%), 0 24px 38px 3px rgb(0 0 0 / 14%), 0 9px 46px 8px rgb(0 0 0 / 12%);
    border-radius: 20px;
    max-width: 100%;
    // filter: blur(2px);
  }

  .motion-card-left {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    padding-left: 5%;
  }

  .motion-card-parent {
    & > .motion-card-child {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      width: 50%;
      text-align: left;
      margin-bottom: 20px;
    }
  }

  .tech-stack-div {
    display: flex;
    justify-content: center;
    align-items: center;
    width: max-content;
    margin: 20px 20px 20px 40px;
    margin-left: 10%;
  }
}

@media only screen and (max-width: 800px) {
  .desktop-app-container {
    align-items: center;
    justify-content: center;
    padding-left: 20px;
    padding: 0;

    .body-text {
      font-size: 14px;
    }

    .motion-card-left {
      padding: 0;
      align-items: center;
      justify-content: center;
    }

    .motion-card-parent {
      & > .motion-card-child {
        display: flex;
        flex-direction: column;
        width: 90%;
        text-align: left;
      }
    }

    .hStack-tech-stack {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
      gap: 0px;
      width: 90vw;
      padding: 0;
    }

    .tech-stack-div {
      margin: 20px;
    }

    .zStack {
      // height: calc(2.8 * 90vw * 0.48);
      // margin-bottom: calc(1.2 * 90vw * 0.53); // (50% + 50% + 10%) * div height = 110% * div height = 1.1 * div height
      overflow: hidden;
      border: 1px solid lightgray;
      padding: 10px;
      width: 95vw;
      border-radius: 20px;
      height: 60vh;
      margin-bottom: 0px;
      box-shadow: rgba(0, 0, 0, 0) 0px 0px 0px 0px, rgba(0, 0, 0, 0) 0px 0px 0px 0px, rgba(0, 0, 0, 0.1) 0px 20px 25px -5px,
        rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
      & > .left-pic-e {
        left: 2%;
      }

      & > .right-pic-e {
        left: 2%;
      }
    }
    .motion-pic {
      box-shadow: rgba(149, 157, 165, 0.3) 0px 12px 48px 0px;
    }
  }
}
